body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.App {
  text-align: center;
  padding: 20px;
}

.App-header {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.menu {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu h2 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  margin: 5px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

.grades-section {
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
  max-width: 600px;
}

.grades-section h2 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.grades-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.grades-table th,
.grades-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}

.grades-table th {
  background-color: #f2f2f2;
}

.grades-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.grades-table tr:hover {
  background-color: #f1f1f1;
}

button.back-to-menu {
  background-color: #2196F3;
  color: white;
}

button.back-to-menu:hover {
  background-color: #1E88E5;
}

.plan-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.plan-cell {
  border: 1px solid #ddd;
  text-align: center;
  padding: 8px;
}

.plan-header {
  border: 1px solid #ddd;
  text-align: center;
  padding: 8px;
  background-color: #f2f2f2;
}