.inbox-section {
    padding: 20px;
    max-width: 700px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .inbox-section h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .messages-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    border: 1px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
  }
  
  .message-item {
    padding: 15px;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    transition: background-color 0.3s ease;
  }
  
  .message-item:last-child {
    border-bottom: none;
  }
  
  .message-item:hover {
    background-color: #f1f1f1;
  }
  
  .message-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .message-from {
    display: block;
    font-size: 18px;
    color: #007bff;
    font-weight: bold;
  }
  
  .message-subject {
    display: block;
    font-size: 16px;
    color: #555;
    font-weight: bold;
  }
  
  .message-body {
    font-size: 14px;
    color: #333;
    margin: 0;
  }
  
  .empty-inbox {
    text-align: center;
    font-size: 16px;
    color: #777;
    margin-top: 20px;
  }



.new-message-section {
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.new-message-section h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.new-message-section button {
    display: block;
    margin: 0 auto 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.new-message-section button:hover {
    background-color: #0056b3;
}

.new-message-section form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.new-message-section label {
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
}

.new-message-section input,
.new-message-section select,
.new-message-section textarea {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    resize: none;
}

.new-message-section input:focus,
.new-message-section select:focus,
.new-message-section textarea:focus {
    border-color: #007bff;
    box-shadow: 0 0 4px rgba(0, 123, 255, 0.5);
    outline: none;
}

.new-message-section button[type="submit"] {
    width: 100%;
    padding: 10px;
    background-color: #28a745;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.new-message-section button[type="submit"]:hover {
    background-color: #218838;
}

.unread-message {
    background-color: #f8d7da; /* Lekko czerwone t≈Ço */
    font-weight: bold;
}

.mark-read-btn {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 5px;
}

.mark-read-btn:hover {
    background-color: #218838;
}

